import React, { useState, useEffect, useRef } from "react";
import { Modal, Button } from "react-bootstrap";
import { FaStar } from "react-icons/fa";
import swal from "sweetalert";

function Example() {

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [name, setName] = useState("");
  const [mobile, setMobile] = useState("");
  const [desc, setDesc] = useState("");

  const [rating, setRating] = useState(null);
  const [hover, setHover] = useState(null);

  const starr = {
    cursor: "pointer",
    transition: "color 200ms",
  };

  /*Validation++++++++++++++++++++++++++++++++*/
  // we use the help of useRef to test if it's the first render
  const firstRender = useRef(true)
  
  // set a state variable which can be used to disable the save/submit button
  // we set it to true so that the form is disabled on first render
  const [disable, setDisabled] = useState(true)

  // we can also set error messages to display to the user
  const [nameError, setNameError] = useState(null)
  const [ratingError, setRatingError] = useState(null)

  // set initial state value(s) for example:
  //const [nameLabel, setNameLabel] = useState('')

  // for every change in our state this will be fired
  // we add validation here and disable the save button if required
  useEffect(() => {
  
    // we want to skip validation on first render
    if (firstRender.current) {
      firstRender.current = false
      return
    }

    // here we can disable/enable the save button by wrapping the setState function
    // in a call to the validation function which returns true/false
    setDisabled(formValidation())
    
  }, [rating]) // any state variable(s) included in here will trigger the effect to run

  // here we run any validation, returning true/false
  const formValidation = () => {
    // if (name === "") {
    //   setNameError('Name cant be blank!')
    //   return true
    // } else {
    //   setNameError(null)
    //   return false
    // };
    if (rating === "") {
      setRatingError('Ratings cant be blank!')
      return true
    } else {
      setRatingError(null)
      return false
    };
  }
  /*-------------------------------*/

  const handleSubmit = (evt) => {
    evt.preventDefault();
    //alert(`Submitting Name ${name}, ${mobile}, ${desc}, ${rating}`)
    addFeedback(name, mobile, desc, rating);
  };

  return (
    <>
      <a className="nav-link" href="#" onClick={handleShow}>
        Feedback
      </a>
      <Modal show={show} onHide={handleClose}>
        <form onSubmit={handleSubmit}>
          <Modal.Header closeButton>
            <Modal.Title>Feedback</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="text-center mb-2">
              {[...Array(5)].map((star, i) => {
                const ratingValue = i + 1;

                return (
                  <label>
                    <input
                      type="radio"
                      name="rating"
                      style={{ display: "none" }}
                      value={ratingValue}
                      onClick={() => setRating(ratingValue)}
                    />
                    { ratingError && <p>{ratingError}</p> }
                    <FaStar
                      style={starr}
                      color={
                        ratingValue <= (hover || rating) ? "#ffc107" : "#e4e5e9"
                      }
                      size={50}
                      onMouseEnter={() => setHover(ratingValue)}
                      onMouseLeave={() => setHover(null)}
                    />
                  </label>
                );
              })}
              <p className="d-none">The rating is {rating} </p>
            </div>
            <div className="form-group row">
              <div className="col-md-12">
                <textarea
                  type="text"
                  className="form-control"
                  value={desc}
                  onChange={(e) => setDesc(e.target.value)}
                  placeholder="Description.."
                  rows="3"
                  style={{ width: "100%" }}
                ></textarea>
              </div>
            </div>
            <div className="form-group row">
              <label className="col-md-3 col-form-label">Name :</label>
              <div className="col-md-9">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Optional.."
                  value={name}
                  name="name"
                  onChange={(e) => setName(e.target.value)}
                />
                { nameError && <p>{nameError}</p> }
              </div>
            </div>
            <div className="form-group row">
              <label className="col-md-3 col-form-label">Mobile No.:</label>
              <div className="col-md-9">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Optional.."
                  value={mobile}
                  onChange={(e) => setMobile(e.target.value)}
                />
              </div>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button
              type="submit"
              value="Submit"
              className="btn btn-primary"
              onClick={handleClose}
              disabled={disable}
            >
              Submit
            </Button>
          </Modal.Footer>
        </form>
      </Modal>
    </>
  );
}

function addFeedback(name, mobile, desc, rating) {
  let data = {};
  data.name = name;
  data.phone = mobile;
  data.designation = desc;
  data.rating = rating;
  data.age = rating;
  fetch("http://localhost:3000/api/employee/store", {
    method: "POST",
    body: JSON.stringify(data),
    headers: {
      "Content-Type": "application/json",
    },
  })
    .then((res) => res.json())
    //.then(response => console.log('Success:', JSON.stringify(response))).then(swal("Thank you!", "Thank you for submitting our feedback!", "success"))
    .then((response) =>
      swal("Thank you!", "Thank you for submitting the feedback!", "success")
    )
    .catch(
      (error) => swal("Oops!", "Something went wrong!", "error")
      //alert("Hi")
    );
  //.catch(error => console.error('Error:', error).then(swal("Oops!", "Something went wrong!", "error")));
}

export default Example;
