import React, { useState } from "react";
import Example from './Popup';
import { Link } from 'react-router-dom';

function SubNavbar() {
  //console.log("Amit2");

  const subNab = {
    background: "gainsboro",
  };
  return (
    <div className="col">
      <div style={subNab} className="justify-content-center row">
        {/* <a
          className="nav-link"
          href="https://infographics.channelnewsasia.com/covid-19/map.html"
          target="_blank"
        >
          Covid-19
        </a> */}
        <Link className="nav-link" to="/covid-19">Covid-19</Link>
        <div className="dropdown">
          <a
            className="nav-link dropdown-toggle"
            data-toggle="dropdown"
            href="#"
            target="_blank"
            aria-haspopup="true"
            aria-expanded="false"
          >
            Services
          </a>
          <div className="dropdown-menu sm-menu dropdown-menu-right">
            <a
              className="dropdown-item"
              href="#"
              onClick={() => alert("Work in Progress....Coming Soon!")}
            >
              Salon at Home
            </a>
            <a className="dropdown-item" href="#">
              Electrician
            </a>
            <a className="dropdown-item" href="#">
              Carpenter
            </a>
          </div>
        </div>

        <Link className="nav-link" to="/reports">Reports</Link>    
        <Link className="nav-link" to="/learning">eLearning</Link>
        {/* Modal */}
        <Example />
      </div>
    </div>
  );
}

export default SubNavbar;
