import React from 'react';
import { Slide } from 'react-slideshow-image';

function SliderImage2() {
    const slideImages = [
        process.env.PUBLIC_URL + '/images/migrant2.jpg',
        process.env.PUBLIC_URL + '/images/migrant3.jpg',
        process.env.PUBLIC_URL + '/images/migrant5.jpg',
        process.env.PUBLIC_URL + '/images/migrant6.jpg',
        process.env.PUBLIC_URL + '/images/migrant8.jpg'
    ];

    const properties = {
        duration: 5000,
        transitionDuration: 500,
        infinite: true,
        indicators: true,
        arrows: true,
        pauseOnHover: true,
        onChange: (oldIndex, newIndex) => {
            //console.log(`slide transition from ${oldIndex} to ${newIndex}`);
        }
    }
    return (
        <div className="slide-container">
            <Slide {...properties}>
                {slideImages.map(image => {
                    return (
                        <div className="each-slide">
                            <div style={{ 'backgroundImage': `url(${image})` }}>
                                {/* <span>Slide 3</span> */}
                            </div>
                        </div>
                    )
                })}
            </Slide>
        </div>
    )
}

export default SliderImage2;

